import {
  App,
  Button,
  Input,
  Textarea
} from '@/demos'

export default [{
  path: '/',
  name: 'App',
  component: App
}, {
  path: '/button',
  name: 'button',
  component: Button
}, {
  path: '/input',
  name: 'Input',
  component: Input
}, {
  path: '/textarea',
  name: 'Textarea',
  component: Textarea
}]



